.title-row {
	margin-top: 0;
	margin-bottom: 0;
	justify-content: left;
}

.title-text {
	margin-left: 10px;
	font-size: 40px;
}

.hyphen-text {
	margin-left: 20px;
	margin-right: 10px;
	font-size: 20px;
}

.subtitle-text {
	margin-left: 10px;
	margin-right: 20px;
	font-size: 20px;
}

.logo {
	margin: 10px;
	width: auto;
	height: auto;
	max-width: 120px;
	max-height: 120px;
}

.main-container {
	height: calc(100vh - 160px);
	display: grid;
	grid-auto-flow: column;
	grid-template-columns: 1fr 2fr;
	margin: 10px;
}

#map-container {
	overflow: hidden;
}
