.data-grid {
	height: 100%;
	width: 100%;
	display: grid;
	grid-auto-flow: row;
	grid-template-rows: 0.5fr 0.5fr 4.5fr 2.5fr;
}

.data-grid .row {
	height: 100%;
}

#showsensors-button {
	background-color: var(--green-light);
	width: 150px;
	height: 55px;
}

.showfeature-button {
	background-color: var(--yellow);
	width: 130px;
	height: 45px;
	margin-right: 15px;
}

.graph {
	height: 100%;
	width: auto;
	justify-content: center;
}

#carousel {
	background-color: var(--gray);
	width: 100%;
	height: 100%;
	margin-top: 0;
	margin-bottom: 0;
	justify-content: center;
}
