.zoomable-image-container {
	position: relative;
	transform-origin: 0% 0%;
}

#slide {
	width: 100%;
	height: 100%;
	transition: transform 0.1s;
}

.map {
	width: 100%;
	height: 100%;
}

.pin {
	transform-origin: 50% 100%;
	position: absolute;
	width: 38px;
	height: 55px;
	top: 35%;
	right: 70%;
	animation: pinRain 1.5s, pinPulse 3s infinite 2.5s;
}

@keyframes pinRain {
	0% {
		top: 0%;
	}
	100% {
		top: 35%;
	}
}

@keyframes pinPulse {
	0% {
		transform: scale(1);
	}
	50% {
		transform: scale(1.5);
	}
	100% {
		transform: scale(1);
	}
}
